import React from 'react';
import { useTranslation } from 'react-i18next';
function KoralatExperience(){
    const { t } = useTranslation();
    return(

        <div id={"koralatExperience"}>
            <h1>Koralat</h1>
            <p><strong>{t('koralatExperienceTitle')}</strong></p>

            <p dangerouslySetInnerHTML={{ __html: t('koralatExperienceDescription') }}></p>

        </div>
    );

}

export default KoralatExperience;